import React from 'react'
import './progress.scss'

class Progress extends React.Component {

  changeProgress(e) {
    let progressBar = this.progressBar;
    let progressP = (e.clientX - progressBar.getBoundingClientRect().left) / progressBar.clientWidth;
    /*bindthisthisreact
    React.createClasses6*/
    this.props.onProgressChange && this.props.onProgressChange(progressP)
  }
  render() {
    return (
      <div
           className="components-progress"
           ref={ (progressBar) => {
                   this.progressBar = progressBar
                 } }
           onClick={ this.changeProgress.bind(this) }>
        <div
             className="progress"
             style={ { width: `${this.props.progress}%`, background: this.props.barColor } }></div>
      </div>
    )
  }
}
export default Progress;


